.card {
  display: flex;
  flex-direction: column;
  width: 100%;
  position: relative;
  text-align: center;
  margin-bottom: 20px;
}

@media (min-width: 768px) {
  .card {
    margin-bottom: 50px;
  }
}

.card__img {
  height: 74px;
  width: 92px;
  margin: 0 auto 10px;
}

.card__img img {
  display: block;
  width: 100%;
}

.card__title {
  font-size: 18px;
  line-height: 22px;
  margin-bottom: 10px;
}

.card__text {
  font-size: 14px;
  line-height: 16px;
  color: rgba(255, 255, 255, 0.5);
}

.btn {
  display: inline-block;
  min-width: 100px;
  border-radius: 10px;
  color: #ffffff;
  text-align: center;
  font-size: 14px;
  padding: 10px;
  transition: all 0.3s ease;
  border: 1px solid #5A81EA;
}

.btn:hover {
  transition: all 0.3s ease;
}

.btn.primary-btn {
  background-color: #5A81EA;
  border-color: #5A81EA;
}

.btn.primary-btn:hover {
  background-color: transparent;
  color: #ffffff;
}

.btn.primary-btn.outline-btn {
  background-color: transparent;
  color: #ffffff;
}

.btn.primary-btn.outline-btn:hover {
  background-color: #5A81EA;
  color: #ffffff;
}

.btn.lg-btn {
  width: 100%;
  height: 50px;
}

@media (min-width: 768px) {
  .btn.lg-btn {
    width: 300px;
  }
}

.hero-section {
  padding: 40px 0 20px;
}

@media (min-width: 768px) {
  .hero-section {
    display: block;
    background: url(../img/photo-1.png);
    background-size: 400px;
    background-repeat: no-repeat;
    background-position: right  top 5%;
    min-height: 440px;
  }
}

@media (min-width: 992px) {
  .hero-section {
    padding: 90px 0 20px;
    display: block;
    background: url(../img/photo-1.png);
    background-size: 500px;
    background-repeat: no-repeat;
    background-position: right 20% top 5%;
    min-height: 440px;
  }
}

.hero {
  margin-bottom: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media (min-width: 768px) {
  .hero {
    display: block;
    max-width: 370px;
    margin-bottom: 76px;
  }
}

.hero__title {
  font-size: 30px;
  line-height: 30px;
  margin-bottom: 20px;
}

@media (min-width: 768px) {
  .hero__title {
    font-size: 36px;
    line-height: 44px;
    margin-bottom: 32px;
  }
}

.hero__box {
  display: flex;
  font-size: 18px;
  line-height: 20px;
  margin-bottom: 20px;
  max-width: 370px;
}

.hero__box > div {
  border-left: 2px solid #5A81EA;
  height: 100%;
  min-height: 100px;
  padding: 0 20px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.hero__percent {
  color: #BE3BFF;
  font-size: 28px;
  line-height: 30px;
}

@media (min-width: 768px) {
  .hero__percent {
    font-size: 36px;
    line-height: 42px;
  }
}

.scoreboard {
  display: block;
  background: rgba(43, 53, 73, 0.3);
  border: 1px solid #506184;
  border-radius: 50px;
  padding: 14px 40px;
  text-align: center;
  margin-bottom: 50px;
}

@media (min-width: 768px) {
  .scoreboard {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 24px 80px;
  }
}

.scoreboard__block {
  margin-bottom: 10px;
}

@media (min-width: 768px) {
  .scoreboard__block {
    margin-bottom: 0;
  }
}

.scoreboard__text {
  color: #959DAE;
  font-size: 16px;
  line-height: 18px;
  padding-bottom: 10px;
}

.scoreboard__price {
  color: #ffffff;
  font-size: 20px;
  line-height: 24px;
}

.scoreboard__title {
  font-size: 24px;
  line-height: 28px;
  text-align: center;
  width: 100%;
  margin-bottom: 22px;
}

.scoreboard__content {
  display: block;
  width: 100%;
}

.scoreboard__content > div {
  margin-bottom: 20px;
}

@media (min-width: 992px) {
  .scoreboard__content > div {
    margin-bottom: 0;
  }
}

@media (min-width: 992px) {
  .scoreboard__content {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
}

.scoreboard__innertext {
  font-size: 24px;
  line-height: 28px;
  padding-left: 24px;
}

.scoreboard__subtext {
  color: rgba(255, 255, 255, 0.5);
  font-size: 15px;
  line-height: 18px;
}

.scoreboard__img {
  height: 30px;
  width: 30px;
}

.scoreboard__img img {
  display: block;
  width: 100%;
}

.scoreboard__list {
  margin: -10px 0;
  font-size: 16px;
  line-height: 18px;
}

@media (min-width: 992px) {
  .scoreboard__list {
    width: 430px;
  }
}

.scoreboard__list li {
  padding: 10px 0;
  color: rgba(255, 255, 255, 0.5);
  border-bottom: 1px dashed rgba(164, 164, 164, 0.5);
}

.scoreboard__list li > div {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.scoreboard__usdt {
  color: #0EAD3A;
}

.scoreboard__value {
  color: #ffffff;
  padding-left: 10px;
}

.scoreboard__valuegray {
  color: rgba(255, 255, 255, 0.5);
}

.scoreboard.scoreboard-lg {
  flex-direction: column;
}

.scoreboard.scoreboard-inner {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 16px 20px;
  border-radius: 5px;
  margin-bottom: 24px;
}

.scoreboard.scoreboard-dashed {
  border: 1px dashed #506184;
}

.scoreboard.scoreboard-space {
  justify-content: space-between;
  font-size: 24px;
  line-height: 28px;
}

.benefits-section {
  background-color: #232C3F;
  padding: 50px 0;
}

.benefits__title {
  font-size: 28px;
