  text-align: center;
  margin-bottom: 30px;
}

@media (min-width: 768px) {
  .benefits__title {
    margin-bottom: 50px;
  }
}

.trade-section {
  padding: 30px 0;
  background: linear-gradient(180deg, rgba(25, 32, 46, 0) 0%, #19202e 34.93%);
}

.trade {
  display: flex;
}

.trade__box {
  margin: 0 0 20px 0;
}

@media (min-width: 768px) {
  .trade__box {
    max-width: 400px;
