  flex-direction: column;
}

main {
  overflow-y: auto;
  overflow-x: hidden;
}

body {
  min-width: 320px;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0 0 10px;
}

img {
  max-width: 100%;
}

p {
  margin: 0 0 10px;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

address {
  font-style: normal;
}

input {
  border: 0;
}

input[type="text"], input[type="password"], input[type="email"], input[type="search"] {
  -webkit-appearance: none;
}

button {
  border: 0;
  padding: 0;
  background-color: unset;
  cursor: pointer;
}

textarea {
  resize: vertical;
  -webkit-appearance: none;
}

.container {
  width: 100%;
  height: 100%;
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 15px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
}

.col-3 {
  display: flex;
  width: 60px;
  padding: 0 15px;
}

@media (min-width: 1024px) {
  .col-3 {
    width: calc(100% / 3);
  }
}

@media (min-width: 1200px) {
  .col-3 {
    width: 25%;
  }
}

.col-4 {
  width: 100%;
  padding: 0 15px;
}

@media (min-width: 1200px) {
  .col-4 {
    width: calc(100% / 3);
  }
}

.col-6 {
  width: 50%;
  padding: 0 15px;
}

.col-9 {
  width: 100%;
  padding: 0 15px;
}

@media (min-width: 1024px) {
  .col-9 {
    width: 66.6%;
  }
}

@media (min-width: 1200px) {
  .col-9 {
    width: 75%;
  }
}

.col-12 {
  padding: 0 15px;
}

@media (min-width: 1200px) {
  .col-12 {
    width: 100%;
  }
}

.d-flex {
  display: flex;
  padding: 0 15px;
  margin-bottom: 10px;
}
